  position: relative;
  width: 40px;
  height: 3px;
  background: #fff;
}

.nav-icon__middle-black {
  background: #393246;
}

.nav-icon__middle::before {
  content: "";
  width: 40px;
  height: 3px;
  background: #fff;
  position: absolute;
  top: -10px;
  left: 0;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}

.nav-icon__middle-black::before {
  background: #393246;
}

.nav-icon__middle::after {
  content: "";
  width: 40px;
  height: 3px;
  background: #fff;
  position: absolute;
  top: 10px;
  left: 0;
  transition: transform 0.2s ease-in, top 0.2s linear 0.2s;
}
.nav-icon__middle::after_grey {
  background: #393246;
}

.nav-icon__middle-black::after {
  background: #393246;
}

.nav-icon__middle.nav-icon-active {
  background: transparent;
  z-index: 999;
}

.nav-icon__middle.nav-icon-active::before {
  transform: rotate(45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
}

.nav-icon__middle.nav-icon-active::after {
  transform: rotate(-45deg);
  top: 0;
  transition: top 0.2s linear, transform 0.2s ease-in 0.2s;
