.contacts {
  padding-bottom: 130px;
}

.contacts__title {
  padding-top: 100px;
  margin-bottom: 100px;
  text-align: center;

  @include tablet {
    padding-top: 70px;
    margin-bottom: 70px;
  }

  @include mobile {
    padding-top: 50px;
    margin-bottom: 50px;
  }
}

.contacts__wrapper {
  display: flex;
  justify-content: center;
  gap: 100px;

  @include laptop-940 {
    flex-direction: column;
    gap: 70px;
  }
}

.contacts__list-flex {
  display: flex;
  flex-direction: column;
  gap: 25px;

  @include laptop-940 {
    flex-direction: row;
    gap: 70px;
  }
  @include mobile-550 {
    flex-direction: column;
    gap: 25px;
  }
}

.contacts__left h3 {
  margin-bottom: 30px;
}

.contacts__left p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 1.5;
  color: #777777;
  margin-bottom: 50px;
}

.contacts__right h3 {
  margin-bottom: 30px;
}

.contacts__list {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 1.5;
  color: #777777;
  margin-left: 32px;
}

.contacts__list a {
  color: #777777;
}

.contacts__list li {
  margin-bottom: 25px;
}

.contacts__list li:last-child {
  margin-bottom: 0;
}

.contacts__list li {
  position: relative;
}

.contacts__list li:nth-child(1)::before {
  position: absolute;
  left: -30px;
  content: "";
  background-image: url(../img/contacts/tel.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  width: 25px;
  height: 25px;
}

.contacts__list li:nth-child(2)::before {
  position: absolute;
  left: -30px;
  content: "";
  background-image: url(../img/contacts/mail.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  width: 24px;
  height: 18px;
}

.contacts__email {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  text-decoration: underline;
  color: #777777;
  transition: 0.2s;

  &:hover {
    text-decoration: underline;
  }
}

.contacts__list li:nth-child(3)::before {
  position: absolute;
  left: -30px;
  content: "";
  background-image: url(../img/contacts/clocks.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  width: 25px;
  height: 25px;
}

.contacts__list li:nth-child(4)::before {
  position: absolute;
  left: -30px;
  content: "";
  background-image: url(../img/contacts/location.svg);
  background-repeat: no-repeat;
  background-size: cover;
  display: block;
  width: 25px;
  height: 25px;
}
