.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 45px;
  padding-bottom: 45px;
}

.nav__list {
  display: flex;
  gap: 50px;

  @include tablet {
    gap: 20px;
  }

  @include mobile {
    display: none;
  }
}

.nav__link {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.14;
  text-transform: uppercase;
  color: #fff;
  transition: 0.2s;

  &:hover {
    color: #fff;
  }
}

.nav__tel a {
  position: relative;
}

.nav__tel a::before {
  content: "";
  background-image: url(../img/header/tel-white.svg);
  position: absolute;
  background-repeat: no-repeat;
  width: 23px;
  height: 25px;
  top: -6px;
  left: -31px;
}

.nav__tel-black a {
  position: relative;
}

.nav__tel {
  @include mobile-550 {
    display: flex;
    gap: 70px;
    align-items: center;
  }
}
